.App {
  display: grid;
  grid-template-areas: "sidebar header header"
    "sidebar body body"
    "sidebar body body";
}

.Sidebar {
  grid-area: sidebar;
}

.Header {
  grid-area: header;
}

.Body {
  grid-area: body;
}
.Header {
  background-color: var(--body-background);
  color: var(--body-color);
  padding: 3rem;
}

.Sidebar {
  height: 100%;
  background-color: var(--sidebar-background);
  color: var(--sidebar-color);
  padding: 2rem;
}

/*Changes that affect all items of sidebar*/

.Sidebar h3 {
  font-size: 1rem;
  font-weight: bolder;
  margin-bottom: .2rem;
}

.Sidebar h2 {
  font-size: 1.3rem;
}

/*Styles over all sidebar items*/
.Sidebar__item {
  margin-bottom: 3rem;
}

.Sidebar__item > ul {
  border-left: 0.05rem solid var(--sidebar-color);
  padding-left: 1.5rem;
  margin-left: 1.5rem;
}
.Sidebar__item:last-child {
  margin-bottom: 0;
}

.Sidebar .AboutMe {
  margin-bottom: 3rem;
  padding-bottom: 3rem;
  border-bottom: 0.05rem solid var(--sidebar-color);
}
.Sidebar__personal-info-section {
  margin-bottom: 3rem;
  padding-bottom: 3rem;
  border-bottom: 0.05rem solid var(--sidebar-color);
}

.Sidebar__item a {
  color: var(--sidebar-color);
  text-decoration: underline;
}

.Sidebar__item a:visited {
  color: var(--sidebar-color);
}
.Body {
  padding: 3rem;
  padding-top: 0;
  color: var(--dark-gray)
}

.Body h2 {
  color: var(--body-color);
  padding-bottom: .5rem;
}

.bottomLine {
  border-bottom: 0.1rem solid var(--body-color);
}

.Body h3 {
  color: var(--body-secondary-color);
}
.AboutMe{
  margin-bottom: 4rem;
}

.AboutMe p {
  line-height: 1.3rem;
}
.Header__title {
  letter-spacing: .3rem;
  font-size: 3rem;
  line-height: .7;
  color: var(--red)
  }

.Header__jobTitle {
  font-size: 1.5rem;
  font-weight: bold;
}
.Education h2 {
  margin-top: 0;
}
.Education__li {
  display: grid;
  grid-template-columns:15rem auto;
}

.Education__li div:nth-child(2) {
  padding-left: 1.5rem;
  margin-left: 1.5rem;
  border-left: .1rem solid var(--light-gray);
}

.Education__li h3 {
  margin: 0;
}

.Education__li-description {
  color: var(--light-gray);
  margin: 0;
  margin-bottom: 1.5rem;
}

.Footer {
  width: 100%;
  text-align: center;
  margin-top: 100%;
}

.Footer small {
  font-size: 1rem;
}

.Footer a {
  color: var(--sidebar-color);
  font-weight: bold;
  text-decoration: underline;
}
.Languages__li {
  display: grid;
  grid-template-columns: 15rem auto;
}

.Languages ul {
  padding: 0;
}
.Languages__li div:nth-child(2) {
  margin-left: 1.5rem;
  padding-left: 1.5rem;
  border-left: 1px solid var(--light-gray);
  font-weight: bold;
  padding-bottom: 1.5rem;
}

.Languages__li h3 {
  margin: 0;
}
/* .Repository__li {
  display: grid;
  grid-template-columns: 15rem auto;
} */

.Repository__project-title {
  margin: 0;
}

.Repository__li div:nth-child(2) {
  padding-left: 1.5rem;
  margin-left: 1.5rem;
  border-left: 1px solid var(--dark-gray);
  margin: 1rem 2px;
  }

.Repository__project-description {
  margin-top: .5rem;
}

.Repository__project-description a {
  color: var(--light-gray);
  text-decoration: underline;
}

.Skills__li {
  margin-bottom: .5rem;
}
